"use client";

export default function Page() {
  const [userId, setUserId] = React.useState("");
  const [features, setFeatures] = React.useState<number[]>([]);
  const [status, setStatus] = React.useState<string>("");

  const capture = async () => {
    // Placeholder: derive a simple feature vector from random seed to simulate retinal pattern
    const seed = Math.random();
    const vec = Array.from({ length: 64 }, (_, i) => Math.sin(seed * (i + 1)));
    setFeatures(vec);
  };

  const enroll = async () => {
    setStatus("enrolling...");
    await capture();
    const res = await fetch("/api/biometric/enroll", { method: "POST", headers: { "Content-Type": "application/json" }, body: JSON.stringify({ userId, features }) });
    const data = await res.json();
    setStatus(res.ok ? "Enrolled" : `Failed: ${data.error || ""}`);
  };

  const verify = async () => {
    setStatus("verifying...");
    await capture();
    const res = await fetch("/api/biometric/verify", { method: "POST", headers: { "Content-Type": "application/json" }, body: JSON.stringify({ userId, features }) });
    const data = await res.json();
    setStatus(res.ok && data.ok ? `Match (${(data.score*100).toFixed(1)}%)` : `No match (${Math.round((data.score||0)*100)}%)`);
  };

  return (
    <div className="mx-auto max-w-3xl space-y-6 p-6 bg-black text-white min-h-screen">
      <h1 className="text-2xl font-semibold text-pink-400">VERIFY EVERYTHING REVEAL NOTHING</h1>
      <div className="rounded-xl border border-pink-700/40 bg-zinc-950 p-5 shadow-[0_0_30px_-10px_rgba(236,72,153,0.5)]">
        <label className="block text-sm">User ID
          <input className="mt-1 w-full rounded-md border border-pink-700 bg-black px-3 py-2 outline-none focus:ring-2 focus:ring-pink-500" value={userId} onChange={(e) => setUserId(e.target.value)} placeholder="userId from registration" />
        </label>
        <div className="mt-3 flex gap-2">
          <button onClick={enroll} className="rounded-md bg-pink-600 px-3 py-2 font-medium text-black hover:bg-pink-500">Enroll</button>
          <button onClick={verify} className="rounded-md border border-pink-600 px-3 py-2 hover:bg-pink-950">Verify</button>
        </div>
        <div className="mt-3 text-sm text-pink-300" aria-live="polite">{status}</div>
      </div>
    </div>
  );
}
